import React from 'react';

const Row = (props) => (
    <tr>
            <td>{props.name}</td>
            <td>{props.role}</td>
            <td>{props.startDate}</td>
            <td>{props.monthsTotal}</td>
            <td>{props.monthlySalary}</td>
            <td>{props.totalBilled}</td>
    </tr>
);

export default Row;


