import React, { Component } from 'react'
import Link from 'gatsby-link'
import firebase from 'firebase'
import PageHeader from 'react-bootstrap/lib/PageHeader'
import Table from 'react-bootstrap/lib/Table'
import Jumbotron from 'react-bootstrap/lib/Jumbotron'

import Row from '../components/Row'

const config = {
  apiKey: 'AIzaSyCQG1sEbCVYYmCxBlhbY0ACZsP25BHh0u0',
  authDomain: 'privet-83a48.firebaseapp.com',
  databaseURL: 'https://privet-83a48.firebaseio.com',
  projectId: 'privet-83a48',
  storageBucket: 'privet-83a48.appspot.com',
  messagingSenderId: '864513816118',
}
firebase.initializeApp(config)
let db = firebase.database()

class IndexPage extends React.Component {
  constructor() {
    super()
    this.state = {
      data: null,
    }
    this.checkDb = this.checkDb.bind(this)
  }

  checkDb() {
    db.ref().on('value', snapshot => {
      this.setState({
        data: snapshot.val(),
      })
    })
  }
  
  componentWillMount() {
    this.checkDb()
  }
  
  render() {
    const data = this.state.data !== null;
    console.log(this.state.data);
    const rows = data ? Object.values(this.state.data).map(x => {
      return (
        <Row
          name={x.name}
          role={x.role}
          startDate={x.startDate}
          monthsTotal={x.monthsTotal}
          monthlySalary={x.monthlySalary}
          totalBilled={x.totalBilled}
        />
      )
    }) : '';
    return (
      <div>
        <Jumbotron>
          <PageHeader>employee total</PageHeader>
        </Jumbotron>
        <Table>
          <thead>
            <tr>
              <th>name</th>
              <th>role</th>
              <th>start date</th>
              <th>months total</th>
              <th>monthly salary</th>
              <th>total billed</th>
            </tr>
          </thead>
          <tbody>{data ? rows : <h1>Loading...</h1>}</tbody>
        </Table>
      </div>
    )
  }
}

export default IndexPage



